.linkItem {
    color: #ff5800;
    cursor: pointer;
    display: inline-block;
    position: relative;
    text-decoration: none;

    &:after {
        content: '';
        border-bottom: 1px solid;
        width: 0;
        opacity: 0.8;
        animation: animateBorder 1s 1;
        height: 1px;
        position: absolute;
        left: 0;
        bottom: -1px;
        transition: width 0.2s linear;
    }

    &:hover {
        &:after {
            width: 100%;
        }
    }
}

.contentWrap {
    padding: 135px 0px 30px 0px;

    .innerContentWrap {
        padding: 15px 45px 15px 45px;
    }

    .weekPickerWrap {
        display: flex;
        margin-bottom: 15px;

        .weekPicker {
            display: flex;
            align-items: center;
            margin-left: auto;

            .btnPrevWeek,
            .btnNextWeek {
                padding: 5px;
                background-color: #dedede;
                font-size: 12px;
                border-radius: 3px;
                color: #ff5800;
                cursor: pointer;
                display: flex;
                align-items: center;
                font-weight: bold;
                transition: 0.5s;

                &:hover {
                    background-color: #ff5800;
                    color: #fff;
                }
            }

            .weekDate {
                margin: 0 10px;
            }
        }
    }

    .loader {
        position: absolute;
        top: 40%;
        left: 45%;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;

        border: 16px solid #f3f3f3; /* Light grey */
        border-top: 16px solid #ff5800; /* Blue */
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 1.5s linear infinite;
    }

    .Facts {
        position: absolute;
        width: 40%;
        top: 65%;
        left: 30%;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

// accordian css
.accordianItem {
    margin-bottom: 15px;
    border: 1px solid #ececec;
    border-radius: 4px;

    .accordianHeader {
        border-radius: 4px;
        background-color: #f5f7fa;
        padding: 5px 15px;
        display: flex;
        align-items: center;
        height: 44px;

        h2 {
            margin: 0;
            font-size: 17px;
            font-weight: 400;
            display: flex;
            align-items: center;
            cursor: pointer;
            color: #333;

            .accordianIcon {
                margin-right: 8px;
                font-size: 12px;
                color: #ff5800;
                transition: 0.5s;
            }

            .accordianHeaderText {
                max-width: calc(100vw - 280px);
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                
            }
        }

        .projectStatusBadge {
            margin-left: auto;
            border-radius: 8px;
            padding: 0 10px 2px;
            color: #fff;
            display: flex;
            animation: blink 1s ease-in-out infinite;
        }
    }

    .accordianPanel {
        display: none;

        .accordianPanelContent {
            padding: 15px;
        }
    }

    &.activeAccItem {
        .accordianHeader {
            h2 {
                color: #000;
                font-weight: 500;

                .accordianIcon {
                    transform: rotate(90deg);
                }
            }
        }

        .accordianPanel {
            display: block;
        }
    }
}

.subProjectWrap {
    border: 1px solid #f3f3f3;
    border-radius: 4px;
    margin-bottom: 15px;
    box-shadow: 0px 3px 6px -1px rgba(0, 0, 0, 0.10);
    &:last-child{
        margin-bottom: 0px;
    }
    .subProjectHeader {
        height: 36px;
        display: flex;
        align-items: center;
        padding: 5px 15px;
        border-radius: 4px;
        background-color: #f3f3f3;

        .subProjectName {
            font-size: 15px;
            font-weight: 500;
            max-width: 350px;
            flex: 1 1 350px;
            margin-right: 15px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;

        }

        .subProjectStatusWrap {
            display: flex;
            margin-left: auto;
            background: #fbfbfb;
            padding: 2px 10px;
            border-radius: 5px;

            .subProjectStatusInner {
                margin-right: 15px;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                justify-content: center;

                &:last-child {
                    margin-right: 0px;
                }

                .subProjectStatus {
                    height: 9px;
                    width: 9px;
                    border-radius: 50%;
                    background-color: #fff;
                    display: inline-block;
                    margin-right: 5px;
                }

                .subProjectType {
                    margin-right: 5px;
                }
            }
        }

        .dashboardLinkWrap {
            margin-left: 80px;
        }
    }

    .subProjectBody {
        padding: 15px;

        .subProjectBodyInner {
            display: flex;

            .highlightPanel {
                flex: 1 1 40%;
                max-width: 40%;
                border-right: 1px solid #ececec;
                min-height: 70px;
                margin-right: 15px;
                padding-right: 15px;
            }

            .riskPanel {
                flex: 1 1 30%;
                max-width: 30%;
                border-right: 1px solid #ececec;
                min-height: 70px;
                margin-right: 15px;
                padding-right: 15px;

                .riskItem {
                    padding: 0 0px 0 15px;
                    margin: 0;
                }
            }

            .workitemPanel {
                flex: 1 1 30%;
                max-width: 30%;
                .workitem{
                    margin-bottom: 5px;
                }
            }
            .panelHead {
                font-weight: 500 !important;
                margin-bottom: 5px;
            }
        }

    }
}

.font500 {
    font-weight: 500 !important;
}



@keyframes blink {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}