/* Normal */
.ms-Checkbox-label {
    display: inline-flex;
    width: 100%;
}

.ms-Checkbox-checkbox {
    height: 17px;
    width: 17px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

.ms-Checkbox-label .ms-Checkbox-checkmark {
    color: #fff;
    font-weight: 700;
    font-size: 11px;
}

.ms-Checkbox-text {
    color: #333;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
}

/* on hover */
.ms-Checkbox-label:hover .ms-Checkbox-checkbox {
    border-color: #ccc;
}

.ms-Checkbox-label:hover .ms-Checkbox-checkmark {
    display: none;
}

/* when checkd */
.ms-Checkbox input:checked+.ms-Checkbox-label .ms-Checkbox-checkbox {
    background-color: #ff5800;
    border-color: #ff5800;
}

.ms-Checkbox input:checked+.ms-Checkbox-label .ms-Checkbox-checkmark {
    display: block;
}


/* Datepicker */

.customDatePicker i.ms-DatePicker-event--without-label {
    color: #ff5800;
    right: 0 !important;
    background: #f5f7fa;
    padding: 6px 7px;
    border-left: 1px solid #d1dbe3;
    top: 0;
    box-sizing: border-box;
}

.customDatePicker .ms-TextField-fieldGroup {
    border: 1px solid #d1dbe3;
    border-radius: 3px;
    box-sizing: border-box;
}

.customDatePicker .ms-TextField-field {
    color: #000;
    font-size: 13px;
    padding: 0 25px 0 8px;
}

.customDatePicker.hideErrMsg .ms-TextField-errorMessage {
    display: none;
}

/* panel css */
.ms-Panel-commands{
    position: absolute;
    right: 10px;
    top: 8px;
    z-index: 1;
}

.ms-Panel-navigation, .ms-Panel-closeButton{
    height: auto;
    width: auto;
    padding: 0;
    margin: 0;
}

.ms-Panel-closeButton i{
    font-size: 14px;
    color: #ff5800;
}



/* custom Tabs */
.ms-Pivot{
    margin-bottom: 10px;
    border-bottom: 1px solid #ececec;
}

.ms-Pivot-link{
    padding: 0;
    line-height: 30px !important;
    height: 30px;
    font-weight: 500;
    color: #000;
    margin-right: 15px;
    font-size: 13px;
}

.ms-Pivot-link.is-selected{
    color: #ff5800;
}

.ms-Pivot-link:before{
    left: 0;
    right: 0;
    content: '';
    border: 0;
    background-color: #ff5800;
    width: 0;
    opacity: 0.7;
    height: 2px;
    position: absolute;
    transition: width 0.2s linear;
}
.ms-Pivot-link.is-selected:before{
    width: 100%;
 }

.ms-Button-flexContainer{
    justify-content: center;
}