.mrAuto {
    margin-right: auto !important;
}

.mlAuto {
    margin-left: auto !important;
}

header {
    position: fixed;
    width: 100%;
    z-index: 1;
    .header {
        display: flex;
        align-items: center;
        height: 90px;
        background-color: #ff5800;
        background: #ff5800;
        background: -moz-linear-gradient(top, #ff5800 0%, #ff8f26 100%);
        background: -webkit-linear-gradient(top, #ff5800 0%, #ff8f26 100%);
        background: linear-gradient(to bottom, #ff5800 0%, #ff8f26 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff5800', endColorstr='#ff8f26', GradientType=0);
        color: #fff;

        padding: 10px 15px;

        .logoWrap {
            display: flex;
            align-items: center;
            font-size: 24px;
            font-weight: 500;
            color: #fff;
            text-decoration: none;
            margin-right: 100px;
            min-width: 300px;

            .logoImage {
                width: auto;
                max-height: 50px;
                margin-right: 15px;
                font-size: 40px;
            }

        }
    }

    .subHeader {
        box-shadow: 0 2px 5px -1px rgba(0,0,0,0.1);
        .subHeaderWrap {
            display: flex;
            margin: 0;
            padding: 5px 15px;
            list-style-type: none;
            height: 45px;
            align-items: center;
            background-color: #f7f7f7;
            border-bottom: 1px solid #f6f6f6;
            // box-shadow: 1px 0px 16px #42424214;
            // box-shadow: 1px 0px 16px rgba(66 66 66 0.8);

            .subHeaderItem {
                .linkItem {
                    color: #ff5800;
                    text-decoration: none;


                    &.homeLink {
                        padding: 5px;
                        display: inline-block;
                        transition: 0.5s;
                        border-radius: 4px;

                        &:hover {
                            background-color: #ff5800;
                            color: #fff;
                        }
                    }

                    .homeIcon {
                        display: flex;
                        font-size: 20px;

                    }
                }

                .userProfile {
                    display: flex;
                    align-items: center;

                    .userIconWrap {
                        padding: 5px 5px 5px 6px;
                        background-color: #ececec;
                        margin-right: 10px;
                        border-radius: 50%;
                        border: 1px solid #d2d2d2;
                        color: #a0a0a0;

                        i {
                            display: flex;
                            font-size: 16px;
                        }
                    }

                    .userName {
                        font-weight: 500;
                    }

                    .divider {
                        border-left: 1px solid;
                        margin: 0 10px;
                        color: #a0a0a0;
                    }

                    .userDesignation {
                        color: #7d7d7d;
                        display: block;
                        max-width: 175px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                }



            }
        }
    }


}