.mrAuto {
    margin-right: auto !important;
}

.mlAuto {
    margin-left: auto !important;
}

.linkItem {
    color: #ff5800;
    cursor: pointer;
    display: inline-block;
    position: relative;
    text-decoration: none;

    &:after {
        content: '';
        border-bottom: 1px solid;
        width: 0;
        opacity: 0.8;
        animation: animateBorder 1s 1;
        height: 1px;
        position: absolute;
        left: 0;
        bottom: -1px;
        transition: width 0.2s linear;
    }
    &:hover {
        &:after {
            width: 100%;
        }
    }
}

footer {
    display: flex;
    width: 100%;
    min-height: 30px;
    background-color: #ff5800;
    padding: 5px 15px;
    position: fixed;
    bottom: 0;

    .footerWrap {
        display: flex;
        list-style-type: none;
        color: #fff;
        align-items: center;
        padding: 0;
        margin: 0;
        // justify-content: center;
        flex-wrap: wrap;
        width: 100%;


        .footerItem {
            margin: 2px 0;
            &:after {
                content: '';
                margin: 0px 20px;
                border: 1px solid #ef996b;
            }

            &:last-child:after, &:first-child:after {
                border: 0;
            }

            .linkItem {
                color: inherit;
            }
        }
    }
}